.content_wrapper{
    display: flex;
    align-items: center;
    height: 100vh;
}
.content_background{
    width: 100%;
    height: 100vh;
    background-image: url('../../img/main-2.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    animation: backgroundTrip 30s infinite;
    position: absolute;
    top: 0;
    left: 0;
}

@keyframes backgroundTrip {
    0%{
        filter: hue-rotate(0deg) brightness(20%);
    }
    50%{
        filter: hue-rotate(360deg) brightness(20%);
    }
    100%{
        filter: hue-rotate(0deg) brightness(20%);
    }
}
.content_block{
    width: 50%;
    background-color: #171717;
    height: 60vh;
    z-index: 10;
    margin: 0 auto;
    color: #EDEDED;
    font-family: 'Roboto-Bold';
}
.content_poem{
    position: absolute;
    width: 250px;
    z-index: 1;
    user-select: none;
    color: #444444;
    top: 10%;
    animation: poemAnim 1000s infinite;
    left: 10px;
    font-size: 16px;
    text-align: left;
    font-family: 'Roboto-Light';
}
@keyframes poemAnim {
    100%{
        transform: translate(0%, -100%);
    }
}
